import React,{useState} from 'react'
import { Link } from 'react-router-dom'
import { NavHashLink } from 'react-router-hash-link'
import './Navbar.css' 
import { FaBars } from 'react-icons/fa'
import logo from '../images/logo.png'


const Navbar = () => {


  const [state, setState] = useState(true)
   
  const toggleBtn = () =>{
    setState(!state)
  }

  
  return (
    <>
     <div className={state ? "navbar" : "navbarOpen"}>
        <div className="logo">
          <img src={logo} alt="logo" />
        </div>
        <div className={state ? "navItems" : "navItemOpen"}>
          <Link to="/gym_website">Home</Link>
          <NavHashLink to="/gym_website/#about">About</NavHashLink>
          <NavHashLink to="/gym_website/#services">Services</NavHashLink>
          <NavHashLink to="/gym_website/#fees">Fees</NavHashLink>
          <Link to="/gallery">Gallery</Link>
          {/* respButton */}
          <div className='resp_btn'>
            <Link to="/contact">
              Contact Us
            </Link>
          </div>
        </div>
        <div className='btn'>
          <Link to="/contact">
            Contact Us
          </Link>
        </div>
        <div className='bars'>
          <FaBars onClick={toggleBtn} />
        </div>
     </div>
    </>
  )
}

export default Navbar
